import riot from 'riot'
import './icons/open-close.tag'

riot.tag2('parts-tree-descripion', '<small>{opts.description}</small>', 'parts-tree-descripion >small,[data-is="parts-tree-descripion"] >small{white-space:pre-line;margin-top:4px;margin-bottom:4px}', '', function(opts) {
    this.on('update', () => {
      this.root.style.display = opts.description ? '' : 'none'
    })
});

import { observe } from 'dob'
import route from 'riot-route'
import * as Actions from '../../actions'
import state from '../../state'
import { getRoutePath } from '../../utils/pattern'
riot.tag2('parts-tree', '<section class="list-line" if="{opts.mdAction.level > 0}"> <parts-icons-open-close riot-style="{getOpenCloseIconStyle()}" is-plus="{opts.mdAction.isOpen}" onclick="{toggleActionBox}"></parts-icons-open-close> <a href="{opts.mdAction.mockUrl}" onclick="{openIframe}" class="{executed: opts.mdAction.mockUrl === lastExecutedUrl}"> {opts.mdAction.name} </a> <section class="labels"> <label each="{switchItem in opts.mdAction.switchs}" class="{parent.getLabelClass(switchItem.name)}" onclick="{() => parent.switchLabel(switchItem.name)}"> {switchItem.name} </label> </section> <parts-tree-descripion description="{getDescription()}"></parts-tree-descripion> </section> <section class="child-tree" riot-style="{getChildTreeStyle()}"> <virtual each="{mdAction in opts.mdAction.mdActions}"> <parts-tree md-action="{mdAction}" class="{mdAction.levelName}"></parts-tree> </virtual> </section>', 'parts-tree,[data-is="parts-tree"]{margin-left:12px;display:block;border-left:1px solid #ccc;box-sizing:border-box} parts-tree >section.list-line,[data-is="parts-tree"] >section.list-line{display:flex;align-items:center;position:relative} parts-tree >section.list-line>parts-icons-open-close,[data-is="parts-tree"] >section.list-line>parts-icons-open-close{position:absolute;top:50%;left:-8px;transform:translateY(-50%)} parts-tree >section.list-line>section.labels>label,[data-is="parts-tree"] >section.list-line>section.labels>label{cursor:pointer;border:1px solid rgba(255,128,0,0.6);padding:0 6px;text-align:center;display:inline-block} parts-tree >section.list-line>section.labels>label.focus,[data-is="parts-tree"] >section.list-line>section.labels>label.focus{background:#ff0} parts-tree >section.list-line>section.labels>label:hover,[data-is="parts-tree"] >section.list-line>section.labels>label:hover{border:1px solid #ff4000} parts-tree >section.list-line>section.labels>label:last-child,[data-is="parts-tree"] >section.list-line>section.labels>label:last-child{margin-right:4px} parts-tree >section.list-line>a,[data-is="parts-tree"] >section.list-line>a{padding:2px 4px;margin-top:2px;margin-bottom:2px;margin-left:14px;margin-right:8px;text-decoration:none;box-sizing:border-box;outline:none} parts-tree >section.list-line>a.executed,[data-is="parts-tree"] >section.list-line>a.executed{border:1px solid #555;border-radius:4px} parts-tree >section.list-line>a:hover,[data-is="parts-tree"] >section.list-line>a:hover{opacity:0.4} parts-tree >section.child-tree>parts-tree.level-1,[data-is="parts-tree"] >section.child-tree>parts-tree.level-1{border:none}', '', function(opts) {

    const openIframe = (e) => {
      route(getRoutePath(opts.mdAction.mockUrl))
      e.preventDefault()
    }

    const toggleActionBox = () => {
      Actions.toggleActionBox(opts.mdAction)
    }

    const getOpenCloseIconStyle = () => opts.mdAction.mdActions.length === 0 && {
      visibility: 'hidden'
    }

    const getChildTreeStyle = () => !opts.mdAction.isOpen && {
      display: 'none'
    }

    const getLabelClass = (name) => ({
      focus: name === opts.mdAction.selectedSwitchName
    })

    const switchLabel = (name) => {
      Actions.setSelectedSwitchName(opts.mdAction, name)
    }

    const getDescription = () => {
      const {
        selectedSwitchName,
        description,
        switchs
      } = opts.mdAction

      if (selectedSwitchName) {
        return switchs.find(switchItem => switchItem.name === selectedSwitchName).description
      }
      return description
    }

    Object.assign(this, {
      lastExecutedUrl: state.mock.lastExecutedUrl,
      getDescription,
      getOpenCloseIconStyle,
      getChildTreeStyle,
      openIframe,
      switchLabel,
      getLabelClass,
      toggleActionBox
    })

    observe(() => {
      void (
        opts.mdAction.isOpen,
        opts.mdAction.selectedSwitchName,
        opts.mdAction.mockUrl
      )

      this.update({
        lastExecutedUrl: state.mock.lastExecutedUrl
      })
    })
});
